.actions {
  text-align: right;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #e7272d;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
  background-color: #e24449;
  border-color: #c5282d;
  color: white;
}

.actions .button--alt {
  color: #e7272d;
}

.actions .button {
  background-color: #e7272d;
  color: white;
}
.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 20rem;
  overflow-y: scroll;
  scroll-behavior: smooth;
  
}
.cartHeight {
  max-height: 15rem;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.text{
  text-align: center;
}